<template>
  <div style="padding: 10px">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/category' }">浏览论文</el-breadcrumb-item>
      <el-breadcrumb-item>{{field_name}}</el-breadcrumb-item>
    </el-breadcrumb>
    <el-table
        v-loading="loading"
        :data="tableData"
        border
        stripe
        style="width: 1100px;margin-top: 10px;">
      <el-table-column
          prop="title"
          label="论文标题"
      >
      </el-table-column>
      <el-table-column
          prop="authors"
          label="论文作者">
      </el-table-column>

      <el-table-column
          prop="conference"
          label="发布会议">
      </el-table-column>
      <el-table-column
          prop="date"
          label="发布时间">
      </el-table-column>
      <el-table-column label="操作" >
        <template #default="scope">
          <el-button size="mini" type="primary" @click="seeDetail(scope.row)">详情</el-button>
          <el-button size="mini" @click="seeFile(scope.row)">附件</el-button>
          <el-button size="mini" @click="deletePaper(scope.row)" >删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <div style="margin: 10px 0">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[1,3,5, 10, 20]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </div>

  </div>
</template>

<script>


import request from "@/utils/request";
import comment from "@/components/Comment";
export default {
  name: 'Role',
  components: {
    comment
  },
  data() {
    return {
      drawer:false,
      loading: true,
      field_id:'',
      paper_id:'',
      field_name:'',
      form: {},
      dialogVisible: false,
      bookVis: false,
      search: '',
      currentPage: 1,
      pageSize: 10,
      total: 0,
      totalPage:1,
      tableData: [],
    }
  },
  created() {
    this.field_id = this.$route.query.field_id
    this.field_name = this.$route.query.field_name
    this.load()

    //this.setCurrentPageData()
  },
  methods: {
    load() {
      this.loading = true
      console.log("okNow")
      let page_no = this.currentPage-1
      request.post("/api/findpaperbyfield?field_id="+this.field_id+"&page_no="+page_no +"&page_size="+this.pageSize).then(res => {
        if (res.code === 0){
          this.tableData = res.data[1]
          this.total = res.data[0]
          console.log(res)
        }
      })
      this.loading = false

    },

    seeDetail(row) {
      console.log("seeDetail: ")
      console.log(row)

      this.$router.push({path: '/detail',query:{paper_id:row.paper_id }})

    },
    seeFile(row) {
      console.log(row)
    },
    deletePaper(row) {
      console.log("seeComment: ")
      console.log(row)
      //this.paper_id = row.paper_id
      this.drawer=true;
    },
    handleSizeChange(pageSize) {   // 改变当前每页的个数触发
      this.pageSize = pageSize
      this.load()
    },
    handleCurrentChange(pageNum) {  // 改变当前页码触发
      this.currentPage = pageNum
      this.load()
    }
  }
}
</script>
